<template>
	<div class="withdrawalDetails">
		<div class="incomeDetails_head">
			<i class="icon_arrow_left" @click="goBack"></i>
			<p class="incomeDetails_title">提现详情</p>
		</div>
		<div class="withdrawalDetails_amount">
			<p class="withdrawalDetails_amount_text">¥<span>{{ detailsObj.amount }}</span></p>
			<p class="withdrawalState">{{ detailsObj.orderStatus == 1?'提现成功':'审核中' }}</p>
		</div>
		<div class="withdrawalDetails_steps">
			<van-divider />
			<p class="withdrawalState">处理进度</p>
			<div class="steps">
				<van-steps direction="vertical" :active="1">
				  <van-step>
				    <p class="title">提现申请</p>
				    <p class="time">{{ detailsObj.createTime }}</p>
				  </van-step>
				  <van-step>
				    <p class="title">审核中，预计5个工作日</p>
				    <p class="time">{{ detailsObj.createTime }}</p>
				  </van-step>
				  <van-step>
				    <p class="title">提现成功</p>
				    <p class="time" v-if="detailsObj.orderStatus == 1">{{ new Date(detailsObj.paymentTime) }}</p>
				  </van-step>
				</van-steps>
			</div>
			
			<van-divider />
		</div>
		<div class="account">
			<p class="account_title">到账账号</p>
			<p class="account_number">微信  {{ detailsObj.nickName }}</p>
		</div>
	</div>
</template>

<script>
	export default {
		name:'withdrawalDetails',
		data(){
			return {
				detailsObj:{}
			}
		},
		mounted() {
			var jsonObj = localStorage.getItem("obj");
			this.obj = JSON.parse(jsonObj);
			this.item = this.$route.query.item;
			this.withdrawalDetail();
		},
		methods:{
			goBack(){
			  this.$router.go(-1);
			},
			withdrawalDetail(){
				let _this = this;
				var exclusiveObj = {
					withdrawalId:_this.item.id
				};
				var requestData = Object.assign(this.obj,exclusiveObj);
				var data = _this.byte256.byte256.encode(_this.key,JSON.stringify(requestData));
				_this.$axios({
				  method: 'post',
				  url: '/withdrawalDetail',
				  data:data
				}).then(function(res) {
				  console.log('withdrawalDetail=========',res);
				  if(res.data.code === 200){
					var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
					console.log(reData);
					var titi = new Date(reData.createTime).toLocaleString('chinese',{hour12:false}).replace(/\//g,'-');
					console.log(titi);
					reData.createTime = titi;
					if('paymentTime' in reData){
						var sisi = new Date(reData.paymentTime).toLocaleString('chinese',{hour12:false}).replace(/\//g,'-');
						reData.paymentTime = sisi;
					}
					
					_this.detailsObj = reData;
				  }else{
					
				  }
				});
			}
		}
	}
</script>

<style scoped>
	.withdrawalDetails{
		width: 10.8rem;
		min-height: 100vh;
		background-color: #fff;
		box-sizing: border-box;
		
	}
	.incomeDetails_head{
		display: flex;
		align-items:center;
		width: 10.8rem;
		height: 2.32rem;
		background-color: #42a5f6;
		box-sizing: border-box;
		padding:var(--barHeight) 0.3rem 0 0.3rem;
		position: relative;
	}
	.icon_arrow_left{
	  display: inline-block;
	  width: 0.56rem;
	  height:0.56rem;
	  background-image: url(../static/img/icon_arrow_left.png);
	  background-repeat: no-repeat;
	  background-size: 100%;
	  position: relative;
	  z-index: 10;
	}
	.incomeDetails_title{
		width: 100%;
		height: 1.52rem;
		line-height: 1.52rem;
		position: absolute;
		left:0;
		bottom:0;
		font-size: 0.48rem;
		color:#fff;
		text-align: center;
		z-index: 9;
	}
	.withdrawalDetails_amount{
		width: 10.8rem;
		height: 4.05rem;
		text-align: center;
		padding-top:0.1rem;
	}
	.withdrawalDetails_amount_text{
		font-size: 0.68rem;
		height: #000;
		line-height: 1.8rem;
		margin-top:0.54rem;
	}
	.withdrawalDetails_amount_text span{
		font-size: 1.02rem;
	}
	.withdrawalState{
		color:#9e9e9e;
		font-size: 0.36rem;
	}
	.withdrawalDetails_steps{
		width: 10.8rem;
		box-sizing: border-box;
		padding: 0 0.3rem;
	}
	.steps .title{
		font-size: 0.48rem;
		color:#000;
		margin-bottom:0.2rem;
		padding-left:0.5rem;
	}
	.steps .time{
		font-size: 0.36rem;
		color:#9e9e9e;
		padding-bottom:1rem;
		padding-left:0.5rem;
	}
	.account{
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 10.8rem;
		box-sizing: border-box;
		padding: 0.54rem 0.3rem;
		font-size: 0.4rem;
		color:#000;
	}
	.account_title{
		font-size: 0.4rem;
		color:#9e9e9e;
	}
</style>
